.App {
  text-align: center;
  font-family: "Libre Franklin", sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: rgb(234, 231, 231);
  text-decoration: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.navigation {
  @media (max-width: 600px) {
    width: 100%;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navbar-brand {
  width: 100px;
}
.customOverlay {
  background: rgba(36, 123, 160, 0.7);
}
.customModal {
  background: #b2dbbf;
  max-width: 500px;
  width: 100%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home,
.about,
.contact {
  background-image: url("https://dsm01pap003files.storage.live.com/y4m521JeH4XTyzP06KTuyWNxMwuM7HjEUKskxhZl7PrhEQiTXkW7xvE4N-sJdOSXesGVcj8dycBPUUOzYZzUCLw3bJnmcSU5SniS1zUnJgMcO9MRxoqNcffQbg-RrP-sBFa09pWTPxITmEz8q05gYo1vMoPmsCSPTMory1n-L0XvQJaKIrhZ_WqsisLEG6aYgdm?width=2048&height=1152&cropmode=none");
  height: 90vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}

.blurb {
  backdrop-filter: blur(5px);
  background-color: rgb(227, 227, 227, 0.4);
  height: 50%;
  padding: 5%;
}

.blurb p {
  font-size: 18px;
}

.contact-list {
  text-decoration: none;
  list-style-type: none;
}

.contact-list li {
  text-decoration: none;
  font-size: 18px;
}

@media (max-width: 600px) {
  .navbar-title {
    display: none;
  }
  .navbar-brand {
    width: 150px;
  }
  .blurb {
    font-size: 14px;
    backdrop-filter: none;
    background-color: transparent;
  }
  .amz-button {
    width: 50% !important;
  }
  .container-header h2 {
    font-size: 16px;
  }
  .container-blur {
    backdrop-filter: blur(5px);
    background-color: rgb(227, 227, 227, 0.4);
  }
}
